nav{
	background: #fff;
	border: none;
	box-shadow: 0 2px 8px 0 rgba(50, 50, 50, 0.04);
	margin: 0 !important;

	
	.navbar-brand {
		font-size: 40px;
		font-weight: bold;
		margin: 0;
		line-height: 40px !important;
	}
	#logo {
		width: 7rem
	}
	.navbar-nav {

		li a {
			color: #606060;
			font-weight: bold;
			padding-right: 20px;
			padding-left: 20px;
			line-height: 40px;
			text-decoration: none;

			&.current{
				color: #FFA823;
				background-color: transparent;
			}
	   }

	   li a:hover{
		   color: #FFA823;
		   background-color: transparent;
	   }
	   
	   .active > a, .active > a:hover, .active > a:focus {
		   color: #FFA823;
		   background-color: transparent;
	   }
	}

	.navbar-toggle {
		border: none;
		padding-top: 12px;
		
		.icon-bar {
			background: #FFA823;
			border-color: transparent;
		}

		&:hover, .navbar-toggle:focus {
			background-color: transparent;
		}
	}

}
